	module.exports = {"pathContext":{"docHtml":"<h1 id=\"workfow-rbac\"><a href=\"#workfow-rbac\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Workfow RBAC</h1>\n<p>All pods in a workflow run with the service account specified in <code>workflow.spec.serviceAccountName</code>,\nor if omitted, the <code>default</code> service account of the workflow's namespace. The amount of access which\na workflow needs is dependent on what the workflow needs to do. For example, if your workflow needs\nto deploy a resource, then the workflow's service account will require 'create' privileges on that\nresource.</p>\n<p>The bare minimum for a workflow to function is outlined below:</p>\n<pre><code class=\"language-yaml\">apiVersion: rbac.authorization.k8s.io/v1\nkind: Role\nmetadata:\n  name: workflow-role\nrules:\n# pod get/watch is used to identify the container IDs of the current pod\n# pod patch is used to annotate the step's outputs back to controller (e.g. artifact location)\n- apiGroups:\n  - \"\"\n  resources:\n  - pods\n  verbs:\n  - get\n  - watch\n  - patch\n# logs get/watch are used to get the pods logs for script outputs, and for log archival\n- apiGroups:\n  - \"\"\n  resources:\n  - pods/log\n  verbs:\n  - get\n  - watch\n</code></pre>","docPath":"argo/workflow-rbac","proj":"argo"}}

